/* home.css */

.App {
    text-align: center;
  }
  
  .musicPlayer {
    position: absolute;
    z-index: -500;
    width: 350px;
  }
  
  .g-background-default {
      background-color: yellow;
  }
  
  .sound__panel {
      background-color: unset;
  }
  
  .widget {
      border: unset;
  }
  
  #playBtn {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
    font-size: 60px;
    color: #bdc3c7;
    cursor: pointer;
  }
  
  .mouseEnter {
    color: white;
  }
  
  #indazooLogo {
    width: 150px;
    height: 80px;
    z-index: 4;
  }
  
  #youtube {
    background-color: black;
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    padding: 3%;
    z-index: 10;
  }
  
  #closeBtn {
    position: fixed;
    font-size: 20px;
    color: #bdc3c7;
    top: 2%;
    right: 2%;
    z-index: 30;
  }
  
  .indazooTitle {
    position: relative;
    top: 0px;
    left: 0px;
  }
  
  .indazooLogo {
    position: fixed;
    width: 10%;
    height: 10%;
    left: 10%;
    bottom: 10%;
    z-index: 5;
  }
  
  #iconGroup {
    position: fixed;
    bottom: 10%;
    right: 5%;
    z-index: 6;
  }
  
  #appMenu a {
    color: white;
    font-size: 20px;
    padding-right: 20px;
    cursor: pointer;
  }
  
  #appMenu a:last-child {
    padding-right: 0;
  }
  
  a:hover {
    text-decoration: none;
  }
  
  .carouselImg {
    /* Set rules to fill background */
    min-height: 100%;
    min-width: 1024px;
      
    /* Set up proportionate scaling */
    width: 100%;
    height: auto;
      
    /* Set up positioning */
    position: fixed;
    top: 0;
    left: 0;
  }
  
  .carousel-control-prev-icon {
    position: fixed;
    left: 5%;
    top: 50%; 
  }
  
  .carousel-control-next-icon {
    position: fixed;
    right: 5%;
    top: 50%; 
  }
  
  .carousel-indicators {
    display:none;
  }
  
  @media screen and (max-width: 1024px) { /* Specific to this particular image */
    .carouselImg {
      left: 50%;
      margin-left: -512px;   /* 50% */
    }
    
  }



