        display: flex;
        width: 100%;
        margin-bottom: 20%;
    }

    .musicDiv {
        display: flex;
        width: 100%;
    }

    /* artist관련 css */
    #artistBox {
        flex-direction: column;
        height: 180%;
    }

    #artistBox .leftCommonBox {
        width: 100%;
        margin-right: unset;
        display: flex;
        margin-bottom: 20%;
        flex-direction: column;
    }

    #artistBox .rightCommonBox {
        width: 100%;
        margin-right: unset;
        display: flex;
    }


    /* shop관련 css */

    #shopContent {
        font-size: 40px;
    }


    /* contact관련 css */
    #contactBox {
        flex-direction: column;
        height: 180%;
    }

    #contactBox .leftCommonBox {
        width: 100%;
        margin-right: unset;
        display: flex;
        margin-bottom: 20%;
        flex-direction: column;
    }

    #contactBox .rightCommonBox {
        width: 100%;
        margin-right: unset;
        display: flex;
    }

    #contactBox .rightCommonBox > div {
        width: 100%;
    }

    #contactRightLastBox {
        margin-top: 5%;
    }

}



/* contact.css */



#contactToggleIcon {
    display: inline-block;
    position: absolute;
    vertical-align: middle;
    margin-top: 10px;
    right: 15px;
    cursor: pointer;
}


.chat {
    width: unset;
    padding: 15px;
    display: flex;
    flex-direction: column;
  }
  
  .messages {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
  }
  
  .message {
    border-radius: 20px;
    padding: 8px 15px;
    margin-top: 5px;
    margin-bottom: 5px;
    display: inline-block;
  }
  
  .yours {
    align-items: flex-start;
  }
  
  .yours .message {
    margin-right: 25%;
    background-color: #eee;
    position: relative;
  }
  
  .yours .message.last:before {
    content: "";
    position: absolute;
    z-index: 0;
    bottom: 0;
    left: -7px;
    height: 20px;
    width: 20px;
    background: #eee;
    border-bottom-right-radius: 15px;
  }
  .yours .message.last:after {
    content: "";
    position: absolute;
    z-index: 1;
    bottom: 0;
    left: -10px;
    width: 10px;
    height: 20px;
    background: white;
    border-bottom-right-radius: 10px;
  }
  
  .mine {
    align-items: flex-end;
  }
  
  .mine .message {
    color: white;
    /* margin-left: 25%; */
    background: linear-gradient(to bottom, #00D0EA 0%, #0085D1 100%);
    background-attachment: fixed;
    position: relative;
  }
  
  .mine .message.last:before {
    content: "";
    position: absolute;
    z-index: 0;
    bottom: 0;
    right: -8px;
    height: 20px;
    width: 20px;
    background: linear-gradient(to bottom, #00D0EA 0%, #0085D1 100%);
    background-attachment: fixed;
    border-bottom-left-radius: 15px;
  }
  
  .mine .message.last:after {
    content: "";
    position: absolute;
    z-index: 1;
    bottom: 0;
    right: -10px;
    width: 10px;
    height: 20px;
    background: white;
    border-bottom-left-radius: 10px;
  }

